body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background: rgb(110, 168, 255);
  margin-top: 30px;
}
.Container {
  width: 220px;
  display: -webkit-flex;
  display: flex;
  background: DodgerBlue;
  border: 1px solid white;
}
.Option {
  position: relative;
  width: 210px;
  color: white;
  padding: 5px;
  font-size: 20px;
  border-bottom: 1px solid rgb(5, 109, 212);
}
.Mets-open::after {
  content: ''; 
  position: absolute; 
  left: 190px; bottom: 0px; 
  border: 10px solid transparent;
  border-top: 10px solid rgb(255, 255, 255);
 }
 .Mets-close::after {
  content: ''; 
  position: absolute; 
  left: 190px; bottom: 10px; 
  border: 10px solid transparent; 
  border-bottom: 10px solid rgb(255, 255, 255);
 }
 .Mets-open, .Mets-close {
  box-shadow: 0 0 50px rgb(214, 231, 255); 
 } 
.Option:hover {
  box-shadow: 0 0 50px rgb(214, 231, 255); 
  cursor: pointer;
  background: rgb(22, 110, 197);
}
.Selected {
  background: rgb(22, 110, 197);
}

