body {
  display: flex;
  justify-content: center;
  background: rgb(110, 168, 255);
  margin-top: 30px;
}
.Container {
  width: 220px;
  display: flex;
  background: DodgerBlue;
  border: 1px solid white;
}
.Option {
  position: relative;
  width: 210px;
  color: white;
  padding: 5px;
  font-size: 20px;
  border-bottom: 1px solid rgb(5, 109, 212);
}
.Mets-open::after {
  content: ''; 
  position: absolute; 
  left: 190px; bottom: 0px; 
  border: 10px solid transparent;
  border-top: 10px solid rgb(255, 255, 255);
 }
 .Mets-close::after {
  content: ''; 
  position: absolute; 
  left: 190px; bottom: 10px; 
  border: 10px solid transparent; 
  border-bottom: 10px solid rgb(255, 255, 255);
 }
 .Mets-open, .Mets-close {
  box-shadow: 0 0 50px rgb(214, 231, 255); 
 } 
.Option:hover {
  box-shadow: 0 0 50px rgb(214, 231, 255); 
  cursor: pointer;
  background: rgb(22, 110, 197);
}
.Selected {
  background: rgb(22, 110, 197);
}
